import React, { useEffect, useState } from 'react'
import MWEditor from './editor/MWEditor'
import MWFileReader from './filereader/MWFileReader'
import Button from '@material-ui/core/Button';
import TextField from '@material-ui/core/TextField';
import { makeStyles } from '@material-ui/core/styles';
import axios from 'axios';
import cogoToast from 'cogo-toast';
import $ from 'jquery';


function BbsNotificationView() {

  const [content, setContent] = useState('');
  const [fileQueue, setFileQueue] = useState([]);

 

  useEffect(() => {
    return ()=>{
      console.log("!!!!!!!!!!!!!!!!!!!!!1");
    }
  })



  const mode = 'edit';// 'edit'!=='edit';
  const styles = {
    root: {
      width: '700px'
    },
    editor: {
      width: '100%',
    },
    fileattach: {
      width: '100%',
      height: '100px',
    },

  }
  const useStyles = makeStyles(theme => ({
    container: {
      display: 'flex',
      flexWrap: 'wrap',
    },
    textField: {
      width: '100%',
    },
    textFieldInput: {
      width: '150px',
    },
    button: {
      margin: theme.spacing(1),
    },
    buttonInc: {
      display: 'flex',
      justifyContent: 'flex-end'
    }

  }));
  const classes = useStyles();

  //1.  (,  )
  //2. file, return  articleid fileUpload
  const handlePreview = (e) => {
    e.preventDefault();
    console.log('handlePreview');
  }
  const handleTempSave = (e) => {
    e.preventDefault();
    console.log('handleTempSave');
  }
  const handleSubmit = (e) => {
    
    e.preventDefault();
    // console.log('handleSubmit',e.target);
    // console.log(data,e.target.writer,e.target.title.value);
    console.log(e.target.title.value);
    console.log(content);

    const data = new FormData(e.target);
    //
    //  :   ,,  ,
    fileQueue.forEach((value,idx,arr)=>{
      data.append('upload',value);
    })
    console.log(data.entries());
    for (var pair of data.entries()) {
      console.log(pair[0] + ',, ' + pair[1]);
    }

    ///////////////////////////////////////////
    const send_param = {
      title: e.target.title.value,
      type: "notification",
      content: content
    }

    axios.post('/boards',send_param,
    {
      headers: {
        'Authorization': 'Bearer ' + $.cookie('accessToken')
    }
    })
    .then(res=>{
      if(res.status === 201){
        cogoToast.success(res.data.message)
        console.log(" .");
        window.location.href="/notice";

      }else{
        cogoToast.error(res.data.message)
      }
    })
    .catch((err) =>{
      console.log(err);

    })
  }





  return (
    <div>
      
      <form id="bbsForm" name="bbsForm" onSubmit={handleSubmit}>
        <div>
          <TextField
            id="title"
            name="title"
            label=""
            className={classes.textField}
            margin="normal"
            variant="outlined" />
        </div>
        <div id="_editor" style={styles.editor}>
          <input type="hidden" id="content" name="content" value={content} />
          <MWEditor mode={mode} content={content} setContent={setContent} />
        </div>
        <div id="fileAttach" style={styles.fileattach}>
          <MWFileReader fileQueue={fileQueue} setFileQueue={setFileQueue} />
        </div>
        <div className={classes.buttonInc}>
          <Button variant="contained" color="default" className={classes.button} onClick={handlePreview}></Button>
          <Button variant="contained" color="default" className={classes.button} onClick={handleTempSave}></Button>
          <Button variant="contained" type="submit" color="secondary" className={classes.button} ></Button>
        </div>
      </form>
    </div>
  )
}

export default BbsNotificationView;
