import React, { Component } from "react";
import { Table, Button } from "react-bootstrap";
import { NavLink } from "react-router-dom";
import axios from "axios";
import $ from "jquery";
import { } from "jquery.cookie";
import cogoToast from "cogo-toast";
import { } from '../../css/pagination.css';


import PostRow from "./PostRow";
import PaginationPostAndComment from "../Pagination/PaginationPostAndComment";


axios.defaults.withCredentials = true;


class PostList extends Component {
    state = {
        posts: [],
        postType: "",
        boardName: "",

        currentPage: 0,
        totalPages: 0,

        search: false,

        searchOption: "title",
        keyword: ""

    }

    Category = () => {
        if (this.props.match.url === '/notice') {
            this.setState({
                postType: "notification",
                boardName: ""
            });
            return "notification";
        } else if (this.props.match.url === '/qna') {
            console.log(" /qna.");
            this.setState({
                postType: "qna",
                boardName: "Q & A"
            }, () => {
                console.log(this.state.postType)
            });
            return "qna";
        }
    }

    componentDidMount() {
        // this.Category();
        if(!this.state.search){
            this.getPosts(this.state.currentPage);
        }

        
        
    }




    getPosts = (pageNumber) => {

        
        console.log(this.state.searchOption);
        console.log(this.state.search);
        console.log(this.state.keyword);
        console.log(this.state.boardName);
        console.log(this.state.postType);
        // const send_param = this.Category();
        let returnType = this.Category();
        let send_params;
        let send_body;
        let url;
        if(this.state.search === true){
            url = `/boards/search`;
            send_params = {
                boardType: returnType,
                page: pageNumber,
                
            };
            send_body ={
                option: this.state.searchOption,
                keyword: this.state.keyword
            };
        }
        
            url = `/boards`;
            send_params = {
                boardType: returnType,
                page: pageNumber
            };


        console.log(send_params.boardType)

        /*  ajax */
        
                axios //  post List 
                    .get(url, {
                        params: send_params,
                        send_body,
                        withCredentials: true
                    })
                    .then(returnData => {
                        const totalPages = returnData.data.totalPages;

                        this.setState({ totalPages: totalPages })

                        if (returnData.data.numberOfElements > 0) {
                            const returnedPosts = returnData.data.content;
                            this.setState({
                                posts: returnedPosts
                            })

                        } else {
                            //   
                            this.state.posts.push([
                                <tr>
                                    <td colSpan="5" style={{ textAlign: "center" }}>  .</td>
                                </tr>
                            ])

                        }
                    })
                    .catch(err => {
                        console.log(err);
                    })


    };

    whitePost = () => {
        axios
            .get(`/boards/auth/${this.state.postType}`,
                {
                    headers: {
                        'Authorization': 'Bearer ' + $.cookie('accessToken'),
                        'Content-Type': 'application/json'
                    }
                })
            .then(response => {
                console.log(response.status);
                //   console.log(this.state.postType);
                if (response.status === 200) {
                    if(this.state.postType === "qna"){
                        this.props.history.push({
                            pathname: "/posts/qna/write",
                            query: { postType: this.state.postType }
                        });
                    }else if(this.state.postType === "notification"){
                        this.props.history.push({
                            pathname: "/posts/notification/write",
                            query: { postType: this.state.postType }
                        });
                    }
                    

                    console.log("     ");


                }
            }).catch(err => {
                console.log(err);
                cogoToast.error("   .");
            });

    }

    ///////////////////////////////////////////////
    paginate = (pageNumber) => {
        this.setState({
            currentPage: pageNumber.selected
        })

        console.log(this.state.currentPage);
        console.log(pageNumber);

            this.getPosts(pageNumber.selected);
            this.forceUpdate();
        
        
        // window.location.reload();
    }


    /////////////////
    handleSearch = (e) =>{
       this.setState({
            keyword: e.target.value
       })
        
    }


    // onSubmit = () =>{
    //     this.setState({
    //         search: true
    //     })
    //     this.getPosts(0);
    // }

    handleSearchOption = (e) =>{
        this.setState({
            searchOption: e.target.value
        })

    }


    render() {
        const divStyle = {
            marginTop: "3%",
            minWidth: "70%",
            minHeight: "700px",
        };



        return (
            <div className="container" style={divStyle}>
                <div style={{ marginBottom: "1%" }}>
                    <h2>{this.state.boardName}</h2>
                </div>

                <div>
                    <h5>{this.state.totalPages}  {this.state.currentPage + 1} </h5>
                </div>
                <div>
                    <Table striped bordered hover size="sm">
                        <thead>
                            <tr style={{ textAlign: "center" }}>
                                <th style={{ width: "10%" }}>#</th>
                                <th style={{ width: "40%" }}></th>
                                <th></th>
                                <th></th>
                                <th></th>
                            </tr>
                        </thead>
                        <tbody>
                            {this.state.posts.map((post) => (
                                <PostRow PostRow={post} />
                            ))}
                        </tbody>
                    </Table>
                    <Button variant="secondary" onClick={this.whitePost}> </Button>
                </div>
                <div>
                    <PaginationPostAndComment totalPage={this.state.totalPages} paginate={this.paginate} />
                </div>
                <div>
        
            
                   
                    <div>
                    <select value={this.state.searchOption} onChange={this.handleSearchOption}>
                        <option value="title">
                            
                        </option>
                        <option value="titleAndContent">
                             + 
                        </option>
                        <option value="author">
                            
                        </option>
                    </select>
                    <input style={{height: 20}}type="text" placeholder="" onChange={this.handleSearch}/>
                    
                        <NavLink 
                        to={{
                            pathname: `/post/search`,
                            data: {
                                postType: this.state.postType,
                                searchOption: this.state.searchOption,
                                keyword: this.state.keyword,
                                boardName: this.state.boardName
                            },
                            
                        }}
                        >
                        {/* <Button></Button> */}
                        <Button>
                            
                        </Button>
                        </NavLink>
                    </div>

                    
                </div>

            </div>

        );
    }
}

export default PostList;