import React, { Component } from "react";
import { Form, Button } from "react-bootstrap";
import axios from "axios";
import {} from "jquery.cookie";
import cogoToast from "cogo-toast"; //,   (   ,   ,  )
axios.default.withCredentials = true; // node.js server   ->  (host)      

class JoinForm extends Component {
  //
  join = () => {
    const joinId = this.joinId.value;
    const joinEmail = this.joinEmail.value;
    const joinName = this.joinName.value;
    const joinPw = this.joinPw.value;
    const telphone = this.telphone.value;


    const telPhoneRegExp = /^\d{3}-\d{3,4}-\d{4}$/;
    const idRegExp = /^[0-9a-zA-Z]{4,15}$/; 
    const emailRegExp = /^[0-9a-zA-Z]([-_.]?[0-9a-zA-Z])*@[0-9a-zA-Z]([-_.]?[0-9a-zA-Z])*.[a-zA-Z]{2,3}$/i;
    const pwRegExp = /^(?=.*[a-zA-Z])(?=.*[!@#$%^*+=-])(?=.*[0-9]).{8,16}$/;
    
    if (joinId === "" || joinId === undefined) {
      cogoToast.warn("  .");
      this.joinId.focus();
      return;
    } 
    else if (
      joinId.match(idRegExp) === null ||joinId.match(idRegExp) === undefined) {
      cogoToast.warn("   .");
      this.joinId.value = "";
      this.joinId.focus();
      return;
    }
    
    if (joinEmail === "" || joinEmail === undefined) {
      cogoToast.warn("  .");
      this.joinEmail.focus();
      return;
    } 
    else if (
      joinEmail.match(emailRegExp) === null ||joinEmail.match(emailRegExp) === undefined) {
      cogoToast.warn("   .");
      this.joinEmail.value = "";
      this.joinEmail.focus();
      return;
    } 
    
    if (joinName === "" || joinName === undefined) {
      cogoToast.warn(" .");
      this.joinName.focus();
      return;
    }

    if (joinPw === "" || joinPw === undefined) {
      cogoToast.warn(" .");
      this.joinPw.focus();
      return;
    } 
    else if ( joinPw.match(pwRegExp) === null || joinPw.match(pwRegExp) === undefined) {
      cogoToast.warn("  ,   8~16 .");
      this.joinPw.value = "";
      this.joinPw.focus();
      return;
    }

    if(telphone.match(telPhoneRegExp) === null || telphone.match(telPhoneRegExp) === undefined){
      cogoToast.warn("   .");
      this.telphone.value = "";
      this.telphone.focus();
      return;
    }

    const send_param = {
      loginId: joinId,
      email: joinEmail,
      name: joinName,
      password: joinPw,
      telephone: telphone
    };
    axios
      .post("/register", send_param)
      // 
      .then(returnData => {
        
        //Id  
        if (returnData.data.dupLoginIdCheck) {
          cogoToast.error(returnData.data.message);
          this.joinId.value = "";
          this.joinId.focus();
        }
        //  
        else if (returnData.data.dupEmailCheck){
          cogoToast.error(returnData.data.message);
          this.joinEmail.value = "";
          this.joinEmail.focus();
        }
        //home 
        else {
          // cogoToast.success(returnData.data.message);
          window.location.href = "/";
        }
        
      })
      //
      .catch(err => {
        console.log(err);
      });
  };

  render() {
    const formStyle = {
      width: "80%"
    };
    const buttonStyle = {
      marginTop: 10
    };
    const centerStyle ={
      marginLeft: "15%",
      marginRight: "15%",
      border:"solid 1px gray",
      backgroundColor:"#FFFFFF"
    }

    return (
    <div style={{backgroundColor:"#edf1f1"}} >
      <center style={centerStyle }>
      <h1 style={{marginTop:"3%"}}></h1>
      <Form  style={formStyle}>
        <Form.Group controlId="joinId">
          <Form.Label>ID</Form.Label>
          <Form.Control
            type="id"
            maxLength="30"
            ref={ref => (this.joinId = ref)}
            placeholder="ID (   4~15  ) ()"
          />
        </Form.Group>
        <Form.Group controlId="joinPw">
          <Form.Label>Password</Form.Label>
          <Form.Control
            type="password"
            maxLength="64"
            ref={ref => (this.joinPw = ref)}
            placeholder="Password ()"
          />
        </Form.Group>
        <Form.Group controlId="joinEmail">
          <Form.Label>Email address</Form.Label>
          <Form.Control
            type="email"
            maxLength="100"
            ref={ref => (this.joinEmail = ref)}
            placeholder="Enter email ()"
          />
          <Form.Text className="text-muted">
            We'll never share your email with anyone else.
          </Form.Text>
        </Form.Group>

        <Form.Group controlId="joinName">
          <Form.Label>name</Form.Label>
          <Form.Control
              type="text"
              maxLength="20"
              ref={ref => (this.joinName = ref)}
              placeholder="name"
          />
        </Form.Group>
        <Form.Group controlId="joinTelPhone">
          <Form.Label>telphone</Form.Label>
          <Form.Control
            type="text"
            maxLength="20"
            ref={ref => (this.telphone = ref)}
            placeholder="telphone (: xxx-xxxx-xxxx)"
          />
        </Form.Group>
        <Form.Group controlId="joinBtn">
          <Button
            style={buttonStyle}
            onClick={this.join}
            variant="dark"
            type="button"
            block
          >
            
          </Button>
        </Form.Group>
      </Form>
      <br/>
      </center>
    </div>
    );
  }
}

export default JoinForm;
