import React, { Component } from "react";
import axios from "axios";
import $ from "jquery";
import {} from "jquery.cookie";
import cogoToast from 'cogo-toast';
import {} from "../../css/commentForm.css";

axios.defaults.withCredentials = true;


class CommentEditor extends Component {
  componentDidMount() {
  }

  createComment = () => {

    // console.log(props)

    if(!$.cookie('accessToken')) {
      cogoToast.error("  !!");
      // window.location.href = "/";   
    }
    let send_param;

    const CommentContent = this.comment.value;

    if (CommentContent === undefined || CommentContent === "") {
      cogoToast.warn("   .");
      CommentContent.focus();
    }
      send_param = {
        boardId: this.props.boardId,
        content: CommentContent
      };

      console.log(this.props.boardId);

    axios
      .post(`/comments`,send_param,
      {
        headers: {
          'Authorization': 'Bearer ' + $.cookie('accessToken')
        }
      })
      .then(response => {
        if(response.status === 201){
          cogoToast.success("  ");
          //.
        }
      })
      .catch(err => {
        cogoToast.error("  ");
      })

      window.location.reload();
  };

  onEnterPress = (e) => {
    if(e.keyCode === 13 && e.shiftKey === false) {
      e.preventDefault();
      this.createComment();
    }
  }


  render() {
    

    return (
      <div className="comment_div">
        <h4>Comment</h4>
          <div className='comment_write'>
            <textarea 
              rows='3'
              ref={ref => (this.comment = ref)}
              maxLength='100'
              placehoder='150   .'
              onKeyDown={this.onEnterPress}
            >
            </textarea>
            <input type='button' id='comment_submit_button' value='' onClick={this.createComment} /> 
          </div>
      </div>
    );
  }
}



export default CommentEditor;
