import React, { Component } from "react";
import { FormControl, Image } from "react-bootstrap";
import { Button, Form, } from 'semantic-ui-react';
import axios from "axios";
import { } from "jquery.cookie";
import searchIcon from '../assets/img/search.png'
import Typist from 'react-typist';
import background from '../assets/img/back.png'
import cogoToast from 'cogo-toast';
import $ from "jquery";


axios.defaults.withCredentials = true;

class ServicePage extends Component {

    constructor(props) {
        super(props);
        this.state = {
            isPaid: false,
        }
    }

    componentDidMount() {
        this.checkPermission()

    }

    //   
    checkPermission = () => {
        axios.get('/api/user/checkPermission').then((res) => {
            if (res.data.message) {
                cogoToast.error(res.data.message);
                this.props.history.push("/login");
                this.setState({
                    isPaid: false
                });
            }
            else {
                this.setState({
                    isPaid: true
                });
            }
        })
    }
    notify = () => {
        cogoToast.success('This is a success message!');
    }

    search = () => {
        const keyword = this.keyword.value;

        if (keyword === "") {
            cogoToast.error("Keyword .");
            return;
        }

        let send_param;
        send_param = {
            keyword: keyword
        }


        axios.post(`/detect`, send_param,
            {
                headers: {
                    'Authorization': 'Bearer ' + $.cookie('accessToken')
                }
            }).then((res) => {
                window.location.href = "/";
                cogoToast.success(res.data.message)
                setTimeout(function () {
                    window.location.href = "/";
                }, 500);
            }).catch((e) => {
                cogoToast.error(e)
            })


    }

    handleKeyPress = (e) => {
        if (e.key === "Enter") {
            this.search()
        }
    };



    render() {

        const container = {
            paddingTop: "10%",
            backgroundImage: `url(${background})`,
            backgroundColor: 'white',
            backgroundSize: 'contain',
            backgroundRepeat: 'no-repeat'
        }

        const titleStyle = {
            marginTop: "6%",
            marginLeft: "30%",
            marginRight: "30%",
            textAlign: 'center',
            color: 'white',
            backgroundColor: '#00ABDF',
        };
        const formStyle = {
            marginTop: "8%",
            width: "50%",
            height: '100%',
            marginLeft: "25%",
            marginRight: "25%",
            marginBottom: "20%",
            paddingLeft: '2%',
            paddingRight: '1%',
            backgroundColor: 'white',
            border: "2px solid #00ABDF",
            borderRadius: "20px",
        };
        const formInputStyle = {
            marginTop: '5px',
            marginBottom: '5px',
            width: '100%',
            height: '40px',
            border: "0px",
            backgroundColor: 'rgba(256,256,256,0.3)',
            textAlign: 'center'
        }
        const buttonStyle = {
            marginTop: '5px',
            marginBottom: '5px',
            height: '40px',
            backgroundColor: 'rgba(256,256,256,0.2)'
        }
        const iconStyle = {
            marginTop: '10px',
            marginBottom: '10px',
            height: '30px',
        }

        return (
            <div className="full-height" style={container}>
                <div style={titleStyle}>
                    <h1>
                        <Typist>
                            Copyright Infringement Detection Site
                        </Typist>
                    </h1>
                </div>
                <Form>
                    <Form.Group style={formStyle}>
                        <Image src={searchIcon} style={iconStyle} />
                        <FormControl style={formInputStyle}
                            ref={ref => (this.keyword = ref)}
                            onKeyPress={this.handleKeyPress}
                            placeholder="keyword ( : Keyword , Keyowrd  )"></FormControl>
                        <Button
                            onClick={this.search}
                            type="button"
                            style={buttonStyle}>
                            GO
                        </Button>
                    </Form.Group>

                </Form>
            </div>
        )
    }
}


export default ServicePage;

