import React, { Component } from "react";
// import moment from 'moment-timezone';
import { Form, Button, Modal } from "react-bootstrap";
import axios from "axios";
import $ from "jquery";
import {} from "jquery.cookie";
import cogoToast from "cogo-toast"; //,   (   ,   ,  )
// import { RepeatOneSharp } from "@material-ui/icons";
axios.defaults.withCredentials = true; // node.js server   ->  (host)      
// axios.defaults.headers.common['x-access-token'] = `test`;
// const instance = axios.create({
// });


class LoginForm extends Component {
  
  state = {
    show: false,
  }
  
  componentDidMount() {

  }
  

  //
  login = () => {
    const loginId = this.loginId.value;
    const loginPw = this.loginPw.value;

    if (loginId === "" || loginId === undefined) {
      cogoToast.warn(" .");
      this.loginId.focus();
      return;
    } else if (loginPw === "" || loginPw === undefined) {
      cogoToast.warn(" .");
      this.loginPw.focus();
      return;
    }

    const send_param = {
      //headers,
      loginId: this.loginId.value,
      password: this.loginPw.value
    };
    axios
      .post("/login", send_param)
      // 
      .then(response => {
        if (response.status === 200) {
          
          const expiresTime = new Date(response.data.data.tokenExpired);
          $.cookie("accessToken", response.data.data.accessToken, { expires: expiresTime });
          // const a = new Date();
          
          cogoToast.success(" .");
          setTimeout(function() {
            window.location.href="/";
          }, 1000);
        } 
        else {
          cogoToast.error(response.data.reason);
          this.loginId.value = "";
          this.loginPw.value = "";
        }
      })
      //
      .catch(err => {
        console.log(err);
      });
  };

  handleKeyPress = (e) => {
    if (e.key === "Enter") {
      this.login();
    }
  };

  render() {
    const buttonStyle = {
      marginTop: 10
    };

    return (
      <Modal show={this.props.show} onHide={this.props.showHandler}>
      <Modal.Header closeButton>
      <Modal.Title></Modal.Title>
      </Modal.Header>
      <Modal.Body>
      <Form className="container">
        <Form.Group>
          <Form.Label>ID</Form.Label>
          <Form.Control
            type="id"
            maxLength="20"
            ref={ref => (this.loginId = ref)}
            placeholder="Enter ID"
            onKeyPress={this.handleKeyPress}
          />
          <br/>
          <Form.Label>Password</Form.Label>
          <Form.Control
            type="password"
            maxLength="20"
            ref={ref => (this.loginPw = ref)}
            placeholder="Password"
            onKeyPress={this.handleKeyPress}
          />
          <Button
            style={buttonStyle}
            onClick={this.login}
            variant="dark"
            type="button"
            block
          >
            
          </Button>
        </Form.Group>
      </Form>
      </Modal.Body>
      </Modal>
     
    );
  }
}

export default LoginForm;
