import React, { useState, useEffect, useRef, useCallback } from 'react';
import Moment from 'moment';
import axios from "axios";
import $ from "jquery";
import { Table, Button } from "react-bootstrap";
import cogoToast from 'cogo-toast';
// import SearchMember from './SearchMember';




const NewUserList = ({ list, total }) => {

    const obsRef = useRef(null); 	//observer Element
    const [List, setList] = useState([]);	//Post List

    const [page, setPage] = useState(0); // 
    const [load, setLoad] = useState(false); // 
    const preventRef = useRef(true); //   
    const endRef = useRef(false); //   

    const [role, setRole] = useState("");

    const[search, setSearch] = useState("");

    // const [totalElement, setTotalElement] = useState(null);


    useEffect(() => { // 
        const observer = new IntersectionObserver(obsHandler, { threshold: 0.5 });
        if (obsRef.current) observer.observe(obsRef.current);
        return () => { observer.disconnect(); }
    }, [])


    useEffect(() => {
        getUser();
    }, [page])


    // 
    const handleRole = (memberId, loginID) => (e) => {
        if(window.confirm("  ?")){
            setRole(e.target.value);
            updateUserRole(memberId, e.target.value);
        }
        
    }

    const obsHandler = ((entries) => { // 
        const target = entries[0];
        if (!endRef.current && target.isIntersecting && preventRef.current) { //   
            preventRef.current = false; //   
            setPage(prev => prev + 1); //  
        }
    })


    const updateUserRole = (memberId, role) =>{

        console.log(role);
        console.log(memberId);
        const send_param = {
            id: memberId,
            role: role
        }

        
        axios
        .put(`/members/${memberId}`, send_param,
        {
          headers: {
            'Authorization': 'Bearer ' + $.cookie('accessToken'),
          }
        })
        .then(response =>{
            if(response.status ===200){
                cogoToast.success(response.data.message);
            }else{
                cogoToast.error("role  ");
            }
        })
        .catch(err => {
            console.log(err);
        });

    };

    const getUser = useCallback(async () => { //   

        setLoad(true); // 

        // ---- Get Data Code ---
        // const res = await axios({ method: 'GET', url: `/members`});
        const res = await axios.get(`/members`, {
            params:{
                page: page
            },
            headers: {
                'Authorization': 'Bearer ' + $.cookie('accessToken'),
              }
        });
        if (res.data) {
            if (res.data.last) { //  
                endRef.current = true;
            }
            setList(prev => [...prev, ...res.data.content]); // 
            preventRef.current = true;
        }
        else {
            console.log(res); //error
        }

        setLoad(false); // 
    }, [page]);

    const createDateFormat = (date) => {
        return Moment(date).format('YYYY-MM-DD HH:mm:ss')
    }





///////////////////


    const handleSearch = (e) =>{
        setSearch(e.target.value);
    }

    const Search = (e) =>{

        setPage(0);
        

        if(search === null || search === ''){
            axios
            .get(`/member/search`, {
                params: {
                    searchWord: search,
                    page: page
                }
            })
        }


    }


    return (
        
        <div className="container">
            {/* <div style={{ marginBottom: "1%" }}>
                <h2>{this.state.boardName}</h2>
            </div>

            <div>
                <h5>{this.state.totalPages}  {this.state.currentPage + 1} </h5>
            </div> */}
            <form onSubmit={e => Search(e)}>
                <input type="text" value={search} placeholder="" onChange={handleSearch}/>
                <Button type='submit'></Button>
            </form>
            <div>
                <Table striped bordered hover size="sm">
                    <thead>
                        <tr style={{ textAlign: "center" }}>
                            <th style={{ width: "20%" }}></th>
                            <th style={{ width: "10%" }}></th>
                            <th style={{ width: "40%" }}></th>
                            <th style={{ width: "10%" }}></th>
                        </tr>
                    </thead>
                    <tbody>
                        {
                            List &&
                            <>
                                {
                                    List.map((user) =>
                                        <tr>

                                            <td style={{ textAlign: "center" }}>
                                                {user.loginId}
                                            </td>
                                            <td style={{ textAlign: "center" }}>
                                                {user.name}
                                            </td>
                                            <td style={{ textAlign: "center" }}>
                                                {createDateFormat(user.createDate)}
                                            </td>
                                            <td style={{ textAlign: "center" }}>
                                                {user.role}
                                            </td>
                                            <td>
                                                <select value={user.role} onChange={handleRole(user.id, user.loginId)}>
                                                    <option value="ADMIN">
                                                        ADMIN
                                                    </option>
                                                    <option value="SUBSCRIBER">
                                                    SUBSCRIBER
                                                    </option>
                                                    <option value="MEMBER">
                                                        MEMBER
                                                    </option>

                                                </select>
                                            </td>
                                        </tr>

                                    )
                                }
                            </>
                        }

                        {
                            load ?
                            <div></div>
                            :
                            <></>
                        }

                    </tbody>
                </Table>

                <div className="py-3 text-center"  ref={obsRef}>...</div>
            </div>
            <div>
                
            </div>

        </div>

    );

}



export default NewUserList;